import Refresh from '@mui/icons-material/Refresh';
import Delete from '@mui/icons-material/Delete';
import {Snackbar,Alert , Container,Button, TableContainer, Table, TableHead,TableBody,TableRow,TableCell,Paper, DialogTitle, DialogContent, makeStyles, colors } from '@mui/material';
import { margin, padding } from '@mui/system';
import { Label } from '@mui/icons-material';
import { blueGrey, red } from '@mui/material/colors';
import axios from 'axios';
import { ErrorMessage } from 'formik';
import Popup from '../../components/Popup';
import Notification from '../../components/Notifications';


import Newbuilding from './NewBuilding';

import { useSettingsContext } from '../../components/settings';

import CustomizedDialogs  from '../../components/Dialog/CustomizedDialogs'

import useTable from '../../components/tables/useTable'

import { variables } from "../../variables";






const tableIcons = {
  Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
  Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
  DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
  Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
  ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
  ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
};



// Style
const columns = [
  {title: "id", field: "id", hidden: true,},
  {title: "Building", field: "buildingName",
},
  {title: "Address", field: "address"},
 
]

// ----------------------------------------------------------------------

export default function Buildings() {
  const { themeStretch } = useSettingsContext();

  const [Building, setBuilding] = useState([]);
  const [iserror, setIserror] = useState(false)
  const [errorMessages, setErrorMessages] = useState([])
  const [notify, setNotify]=useState({isOpen:false, message:'', type:''})
  const [openPopup, setOpenPopup]=React.useState(false);

  
  
const getBuildings = async()=>
{
  try 
  {
    const Endpoint = `Building`
    const data = await axios.get(variables.API_URL+Endpoint);
   setBuilding(data.data)
    
  }
  catch (e){
    console.log(e);
  }
}



useEffect(()=>{



  
  getBuildings();



},[]);


// Update Row

const handleRowUpdate = (newData, oldData, resolve) => {
  // validation
  const errorList = []
  if(newData.buildingName === ""){
    errorList.push("Please enter build name")
  }
  if(newData.address === ""){
    errorList.push("Please enter Address name")
  }

  if(errorList.length < 1){
 //   api.patch("/users/"+newData.id, newData)

 const Endpoint = `Building`

    axios.put(variables.API_URL+Endpoint,newData)
    .then(res => {
      const dataUpdate = [...Building];
      const index = oldData.tableData.id;
      dataUpdate[index] = newData;
      setBuilding([...dataUpdate]);
      resolve()
      setIserror(false)
      setErrorMessages([])
      setNotify({
        isOpen:true,
        message: 'Sucessfully Updated',
        type: 'success'
    },
   
        )
    })
    .catch(error => {
      setErrorMessages(["Dhib dhan serverka ah! Server error"])
      setIserror(true)
      resolve()
       
    })
  }else{
    setErrorMessages(errorList)
    setIserror(true)
    resolve()

  }
   
}


// Delete Row

const handleRowDelete = (oldData, resolve) => {

  const Endpoint = `Building/${oldData.id}`
 
  axios.delete(variables.API_URL+Endpoint)
      .then(res => {
        const dataDelete = [...Building];
        const index = oldData.tableData.id;
        dataDelete.splice(index, 1);
        setBuilding([...dataDelete]);
        resolve();
        setOpenPopup(false);
        setNotify({
          isOpen:true,
          message: 'Sucessfully Deleted',
          type: 'error'
      } 
          )
      })
      .catch(error => {
        setErrorMessages(["Delete failed! Server error"])
        setIserror(true)
        console.log(ErrorMessage)
        resolve()
      })
  }

  
// Insert 

const inserdata = (values)=>{
  const data = {
    records:[
        {
            fields:{
                buildingName: values.buildingName,
                address: values.address
            },
        },
    ],
};

const axiosConfig = {
    headers:{
        'Content-Type':'application/json',
    },
   
};
const Endpoint = `Building`
axios.post(variables.API_URL+Endpoint,{buildingName: values.buildingName,address:values.address})
.then((response)=>{

    setNotify({
        isOpen:true,
        message: 'Submitted Successfully',
        type: 'success'
    } 
        )
        setOpenPopup(false);
   
})
.catch((e)=>{
    console.log("Hello")
})
}


const handleClose = () => {
  setOpenPopup(false)
};


// Table Data


  return (

    <>
    <Helmet>
      <title> HR- Buildings</title>
    </Helmet>

    <Container maxWidth={themeStretch ? false : 'x3'} sx={{margin:0}}>

    <Button variant="outlined" onClick={()=>setOpenPopup(true)}>
        New Building
      </Button>
        <div>
        {errorMessages.map((msg, i) => {
                      return <div key={i}>{msg}</div>
                  })}
        </div>
        <MaterialTable mt={90}
              title="Building Details"
              columns={columns}
              data={Building}
              icons={tableIcons}
              options={{
                
    headerStyle: {
      backgroundColor: "#F4F6F8",
          color: "#637381",
        
    },
    rowStyle: {
      
      border: '2px solid #eee'
    },
  
              }}

            

              editable={{
                onRowUpdate: (newData, oldData) =>
                  new Promise((resolve) => {
                      handleRowUpdate(newData, oldData, resolve);
                       
                  }),

                  onRowDelete: (oldData) =>
                  new Promise((resolve) => {
                    handleRowDelete(oldData, resolve)
                  }),
                  
                }}

              


              
            />
    </Container>

<Popup
openPopup={openPopup}
setOpenPopup={setOpenPopup}
title="New Building"
>
<Newbuilding inserdata={inserdata}  setOpenPopup={setOpenPopup} />

</Popup>




    <Notification
        notify={notify}
        setNotify={setNotify}
        />
  </>


  );
}

