import {React,useState  }from 'react'
import { Helmet } from 'react-helmet-async';
// @mui
import { Container, Typography,Grid, Paper, Button, TextField } from '@mui/material';
import axios from 'axios';
import { Formik, Form, Field, ErrorMessage } from 'formik'
import * as Yup from 'yup'
import Notification from '../../components/Notifications';
// components
import { useSettingsContext } from '../../components/settings';




export default function NewBuilding(props) {

    const {inserdata,setOpenPopup}= props
    
    const paperStyle = { padding: '0 15px 40px 15px', width: 500, }
    const btnStyle = { marginTop: 10 }
    const [notify, setNotify]=useState({isOpen:false, message:'', type:''})
    const initialValues = {
        buildingName: '',
        address: '',
    
    }
    const validationSchema = Yup.object().shape({
        buildingName: Yup.string().min(3, "It's too short").required("Required"),
        
        address : Yup.string().min(3, "It's too short Address").required("Required"),
        
    })
    const onSubmit = (values, props) => {
    
        inserdata(values)

    }

  const CancelHandle=()=>
  {
    setOpenPopup(false)
  }

    

  return (
    <>
     <Grid>
            <Paper elevation={0} style={paperStyle}>
                <Grid align='center'>
                    <Typography variant='caption'>Fill the form to an new building</Typography>
                </Grid>
                <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit}>
                    {(props) => (
                        <Form noValidate>
                         

                            <Field as={TextField} name='buildingName' label='Building' fullWidth
                                error={props.errors.buildingName && props.touched.buildingName}
                                helperText={<ErrorMessage name='buildingName' />}  />

<Field as={TextField} name='address' label='Address' fullWidth
                                error={props.errors.address && props.touched.address}
                                helperText={<ErrorMessage name='address' />} />


                       
                         

                            


                            <Button type='submit' style={btnStyle} variant='contained'
                                color='primary' >Create</Button>
                                <Button onClick={CancelHandle} color="error">Cancel</Button>
                        </Form>
                    )}
                </Formik>
            </Paper>
        </Grid>

        <Notification
        notify={notify}
        setNotify={setNotify}
        />
    </>
  );
}
